import { HTMLAttributes, ReactNode } from 'react';

import classNames from 'classnames';
import './index.scss';

// ---------------------------------------------------------------------- //

type HeadSize = 'xl' | 'lg' | 'base' | 'sm' | 'xs' | 'option';
type BodySize = 'lg' | 'base' | 'sm' | 'xs' | 'option';

export type TypographyProps = {
  variant?: 'head' | 'body' | 'caption';
  size?: HeadSize | BodySize;
  align?: 'left' | 'center' | 'right';
  weight?: 400 | 500 | 700;
  color?: string;
  isParagraph?: boolean;
  children: ReactNode;
} & HTMLAttributes<HTMLBaseElement>;

const HEAD_SIZE_MAPS: Record<HeadSize, string> = {
  xl: 'text-head-xl',
  lg: 'text-head-lg',
  base: 'text-head-base',
  sm: 'text-head-sm',
  xs: 'text-head-xs',
  option: 'text-head-option'
};

const BODY_SIZE_MAPS: Record<BodySize, string> = {
  lg: 'text-body-lg',
  base: 'text-body-base',
  sm: 'text-body-sm',
  xs: 'text-body-xs',
  option: 'text-body-option'
};

const ALIGN_MAPS = {
  left: 'text--left',
  center: 'text--center',
  right: 'text--right'
};

const WEIGHT_MAPS = {
  400: 'font-400',
  500: 'font-500',
  700: 'font-700'
};

const windowsStyle = window.navigator.userAgent.includes('Win') ? 'rotate-[0.03deg]' : undefined;

const Typography = ({
  variant = 'body',
  size = 'base',
  align = 'left',
  weight,
  color,
  isParagraph = false,
  children,
  className
}: TypographyProps) => {
  return (
    <>
      {variant === 'head' && (
        <strong
          className={classNames(
            HEAD_SIZE_MAPS[size as HeadSize],
            ALIGN_MAPS[align],
            WEIGHT_MAPS[weight || 700],
            color,
            windowsStyle,
            className
          )}
        >
          {children}
        </strong>
      )}
      {variant === 'body' && (
        <span
          className={classNames(
            BODY_SIZE_MAPS[size as BodySize],
            ALIGN_MAPS[align],
            WEIGHT_MAPS[weight || 400],
            'break-keep',
            color,
            windowsStyle,
            { 'whitespace-pre-wrap break-keep': isParagraph },
            className
          )}
        >
          {children}
        </span>
      )}
      {variant === 'caption' && (
        <span
          className={classNames(
            'text-caption',
            ALIGN_MAPS[align],
            WEIGHT_MAPS[weight || 400],
            'break-keep',
            color,
            windowsStyle,
            { 'whitespace-pre-wrap break-keep': isParagraph },
            className
          )}
        >
          {children}
        </span>
      )}
    </>
  );
};

export default Typography;
